"use client"

import { useEffect, useState, useCallback, useRef } from "react"
import { useRouter } from "next/navigation"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Avatar, AvatarFallback } from "@/components/ui/avatar"
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Card } from "@/components/ui/card"
import { 
  Send, User, MessageSquare, Loader2, Hash, 
  Plus, Search, MoreVertical, Settings, ArrowLeft, LogOut
} from "lucide-react"

import { Client } from '@stomp/stompjs'
import SockJS from 'sockjs-client'

export default function RealTimeChatPage() {
  const router = useRouter()
  
  const [userId, setUserId] = useState("")
  const [userName, setUserName] = useState("")
  const [activeTab, setActiveTab] = useState("GROUP") 
  const [rooms, setRooms] = useState<any[]>([])
  const [searchQuery, setSearchQuery] = useState("")
  const [currentRoom, setCurrentRoom] = useState<any>(null)
  const [messages, setMessages] = useState<any[]>([])
  const [inputValue, setInputValue] = useState("")
  const [isConnected, setIsConnected] = useState(false)
  const [isLoadingHistory, setIsLoadingHistory] = useState(false)
  
  const stompClient = useRef<Client | null>(null)
  const scrollRef = useRef<HTMLDivElement>(null)
  const subscriptionRef = useRef<any>(null)

  const API_BASE = 'http://sparta-nextme.xyz:18080'

  const loadRooms = useCallback(async (type: string) => {
    const token = localStorage.getItem("accessToken")
    if (!token) return
    try {
      const res = await fetch(`${API_BASE}/v1/chats/room?roomType=${type}`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const roomList = data.rooms || data.result || (Array.isArray(data) ? data : [])
      setRooms(roomList)
    } catch (e) { setRooms([]) }
  }, [])

  const loadChatHistory = async (roomId: string) => {
    const token = localStorage.getItem("accessToken")
    setIsLoadingHistory(true)
    try {
      const res = await fetch(`${API_BASE}/v1/chats/message/${roomId}?size=50`, {
        headers: { "Authorization": `Bearer ${token}` }
      })
      const data = await res.json()
      const rawMessages = Array.isArray(data) ? data : (data.messages || data.result || [])
      setMessages([...rawMessages].reverse())
    } catch (e) { setMessages([]) } finally { 
      setIsLoadingHistory(false)
      scrollToBottom()
    }
  }

  const selectRoom = async (room: any) => {
    const roomId = room.chatRoomId || room.id
    if (currentRoom?.chatRoomId === roomId || currentRoom?.id === roomId) return
    const token = localStorage.getItem("accessToken")

    try {
      const joinRes = await fetch(`${API_BASE}/v1/chats/room/${roomId}/join`, {
        method: 'POST',
        headers: { 'Content-Type': 'application/json', 'Authorization': `Bearer ${token}` }
      })

      if (joinRes.ok) {
        if (subscriptionRef.current) subscriptionRef.current.unsubscribe()
        setCurrentRoom(room)
        await loadChatHistory(roomId)
        
        // ★ 입장 알림 추가
        const entryMessage = {
          content: `${userName}님이 입장하셨습니다.`,
          senderName: "System",
          isSystem: true,
          createdAt: new Date().toISOString()
        }
        setMessages(prev => [...prev, entryMessage])

        if (stompClient.current?.connected) {
          subscriptionRef.current = stompClient.current.subscribe(`/topic/chat.room.${roomId}`, (msg) => {
            const received = JSON.parse(msg.body)
            
            // 1. 메시지 목록 업데이트
            setMessages((prev) => [...prev, received])
            
            // 2. ★ 왼쪽 사이드바 마지막 메시지 실시간 업데이트
            setRooms((prevRooms) => prevRooms.map(r => {
                const rId = r.chatRoomId || r.id
                if (rId === roomId) {
                    return { ...r, lastMessage: received.content }
                }
                return r
            }))
            
            scrollToBottom()
          })
        }
      }
    } catch (e) { console.error(e) }
  }

  const handleLeaveRoom = async () => {
    const roomId = currentRoom?.chatRoomId || currentRoom?.id
    if (!roomId) return

    if (confirm("이 대화방에서 나가시겠습니까?")) {
      const token = localStorage.getItem("accessToken")
      const uId = localStorage.getItem("userId")
      
      try {
        const res = await fetch(`${API_BASE}/v1/chats/room/${roomId}/leave`, {
          method: "POST",
          headers: { "Authorization": `Bearer ${token}`, "Content-Type": "application/json" },
          body: JSON.stringify({ userId: uId })
        })

        if (res.ok) {
          if (subscriptionRef.current) subscriptionRef.current.unsubscribe()
          setCurrentRoom(null)
          setMessages([])
          loadRooms(activeTab)
          alert("방에서 퇴장하였습니다.")
        }
      } catch (e) { console.error("퇴장 실패:", e) }
    }
  }

  const handleSendMessage = (e?: React.FormEvent) => {
    if (e) e.preventDefault()
    if (!inputValue.trim() || !stompClient.current?.connected || !currentRoom) return
    const roomId = currentRoom.chatRoomId || currentRoom.id
    stompClient.current.publish({
      destination: `/app/chat.send/${roomId}`,
      body: JSON.stringify({ content: inputValue, roomType: currentRoom.roomType || activeTab }),
    })
    setInputValue("")
    scrollToBottom()
  }

  useEffect(() => {
    const token = localStorage.getItem("accessToken")
    if (!token) return router.push("/login")
    setUserId(localStorage.getItem("userId") || "")
    setUserName(localStorage.getItem("name") || "사용자")

    const client = new Client({
      brokerURL: `ws://sparta-nextme.xyz:18080/ws/chat`,
      connectHeaders: { Authorization: `Bearer ${token}` },
      webSocketFactory: () => new SockJS(`${API_BASE}/ws/chat`),
      reconnectDelay: 5000,
      onConnect: () => { setIsConnected(true); loadRooms(activeTab); },
      onDisconnect: () => setIsConnected(false)
    })
    client.activate()
    stompClient.current = client
    return () => { if (stompClient.current) stompClient.current.deactivate() }
  }, [activeTab, loadRooms, router])

  const scrollToBottom = () => {
    setTimeout(() => { if (scrollRef.current) scrollRef.current.scrollTop = scrollRef.current.scrollHeight }, 100)
  }

  return (
    <div className="flex h-screen w-screen bg-background text-foreground overflow-hidden font-sans">
      <aside className="w-80 md:w-96 flex flex-col bg-card border-r border-border shadow-2xl z-20">
        <div className="p-6 space-y-6">
          <div className="flex items-center gap-4">
            <Button variant="secondary" size="icon" onClick={() => router.push("/dashboard")} className="h-10 w-10 rounded-xl bg-secondary/50 hover:bg-primary/20 hover:text-primary transition-all shadow-sm">
              <ArrowLeft className="h-5 w-5" />
            </Button>
            <h1 className="text-lg font-black tracking-tight">메시지</h1>
          </div>
          <div className="relative">
            <Search className="absolute left-3 top-1/2 -translate-y-1/2 h-4 w-4 text-muted-foreground/50" />
            <Input placeholder="대화 검색" value={searchQuery} onChange={(e) => setSearchQuery(e.target.value)} className="w-full pl-10 h-11 bg-secondary/30 border-none rounded-xl text-sm focus-visible:ring-1 focus-visible:ring-primary/40" />
          </div>
          <Tabs value={activeTab} onValueChange={(v) => { setActiveTab(v); setCurrentRoom(null); }}>
            <TabsList className="grid grid-cols-2 w-full bg-secondary/50 p-1 h-11 rounded-xl">
              <TabsTrigger value="GROUP" className="rounded-lg font-bold text-xs data-[state=active]:bg-background data-[state=active]:text-primary data-[state=active]:shadow-md">그룹 채널</TabsTrigger>
              <TabsTrigger value="DIRECT" className="rounded-lg font-bold text-xs data-[state=active]:bg-background data-[state=active]:text-primary data-[state=active]:shadow-md">1:1 메시지</TabsTrigger>
            </TabsList>
          </Tabs>
        </div>
        <div className="flex-1 overflow-y-auto px-4 pb-6 space-y-2 custom-scrollbar">
          {rooms.filter(r => (r.title || r.name || "").toLowerCase().includes(searchQuery.toLowerCase())).map((room: any) => {
            const isSelected = (currentRoom?.chatRoomId || currentRoom?.id) === (room.chatRoomId || room.id);
            return (
              <button key={room.chatRoomId || room.id} onClick={() => selectRoom(room)} className={`w-full p-4 rounded-2xl flex items-center gap-4 transition-all duration-300 ${isSelected ? 'bg-primary/10 ring-1 ring-primary/30' : 'hover:bg-secondary/30'}`}>
                <div className={`h-11 w-11 rounded-2xl flex items-center justify-center shrink-0 transition-all ${isSelected ? 'bg-primary text-primary-foreground shadow-lg' : 'bg-secondary text-muted-foreground'}`}>
                  {activeTab === "GROUP" ? <Hash className="h-5 w-5" /> : <User className="h-5 w-5" />}
                </div>
                <div className="text-left overflow-hidden flex-1">
                  <p className={`font-bold truncate text-[14px] ${isSelected ? 'text-primary' : 'text-foreground'}`}>{room.title || room.name}</p>
                  {/* ★ 마지막 메시지 표시 복구 */}
                  <p className="text-[11px] text-muted-foreground truncate font-medium mt-0.5">{room.lastMessage || "새로운 메시지가 없습니다."}</p>
                </div>
              </button>
            )
          })}
        </div>
        <div className="p-4 bg-secondary/20 border-t border-border">
           <div className="flex items-center gap-3 p-2 bg-card rounded-2xl border border-border/50 shadow-sm">
              <Avatar className="h-10 w-10 ring-2 ring-secondary">
                <AvatarFallback className="bg-primary text-primary-foreground font-bold text-xs">{userName?.charAt(0)}</AvatarFallback>
              </Avatar>
              <div className="flex-1 min-w-0">
                <p className="text-[13px] font-bold truncate">{userName}님</p>
                <div className="flex items-center gap-1.5">
                  <div className={`h-1.5 w-1.5 rounded-full ${isConnected ? 'bg-green-500 animate-pulse' : 'bg-red-500'}`} />
                  <span className="text-[9px] font-black uppercase text-muted-foreground tracking-tighter">{isConnected ? 'Online' : 'Offline'}</span>
                </div>
              </div>
              <Button variant="ghost" size="icon" onClick={() => router.push("/dashboard/settings")} className="h-8 w-8 text-muted-foreground hover:text-primary"><Settings className="h-4 w-4" /></Button>
           </div>
        </div>
      </aside>

      <main className="flex-1 flex flex-col bg-background relative">
        {currentRoom ? (
          <>
            <header className="h-[76px] px-8 border-b border-border flex items-center justify-between shrink-0 bg-card/30 backdrop-blur-xl z-10">
              <div className="flex items-center gap-4">
                <div className="h-11 w-11 rounded-2xl bg-secondary/50 flex items-center justify-center text-primary border border-primary/10">
                  {activeTab === "GROUP" ? <Hash className="h-5 w-5" /> : <User className="h-5 w-5" />}
                </div>
                <div>
                  <h2 className="font-black text-base tracking-tight">{currentRoom.title || currentRoom.name}</h2>
                  <p className="text-[10px] text-primary font-bold uppercase tracking-widest">실시간 연결 중</p>
                </div>
              </div>
              <div className="flex items-center gap-3">
                <Button variant="ghost" size="sm" onClick={handleLeaveRoom} className="text-xs font-bold text-muted-foreground hover:text-destructive hover:bg-destructive/10 px-3 h-9 rounded-xl transition-colors">
                  <LogOut className="h-4 w-4 mr-2" /> 채널 나가기
                </Button>
                <div className="w-[1px] h-4 bg-border" />
                <Button variant="secondary" size="icon" className="h-9 w-9 rounded-xl"><MoreVertical className="h-5 w-5" /></Button>
              </div>
            </header>

            <div ref={scrollRef} className="flex-1 overflow-y-auto px-8 py-8 space-y-6 custom-scrollbar bg-gradient-to-b from-background to-secondary/10">
              {isLoadingHistory && <div className="flex justify-center p-4"><Loader2 className="h-6 w-6 animate-spin text-primary/60" /></div>}
              {messages.map((msg, idx) => {
                const isMine = msg.senderId === userId
                
                // ★ 입장 알림(시스템 메시지) 스타일
                if (msg.isSystem) {
                  return (
                    <div key={idx} className="flex justify-center my-2">
                      <div className="bg-secondary/40 text-muted-foreground text-[11px] font-bold px-4 py-1.5 rounded-full border border-border/50">
                        {msg.content}
                      </div>
                    </div>
                  )
                }

                return (
                  <div key={idx} className={`flex ${isMine ? "justify-end" : "justify-start"} animate-in fade-in slide-in-from-bottom-3 duration-500`}>
                    <div className={`flex flex-col space-y-1.5 max-w-[75%] ${isMine ? "items-end" : "items-start"}`}>
                      {!isMine && <span className="text-[11px] font-black text-muted-foreground/70 ml-1">{msg.senderName}</span>}
                      <Card className={`p-4 text-sm font-medium leading-relaxed border-none shadow-xl ${isMine ? "bg-primary text-primary-foreground rounded-2xl rounded-tr-none shadow-primary/20" : "bg-card text-foreground rounded-2xl rounded-tl-none ring-1 ring-border/50"}`}>
                        {msg.content}
                      </Card>
                      <span className="text-[9px] text-muted-foreground/40 font-bold px-1 uppercase tracking-tighter">
                        {msg.createdAt ? new Date(msg.createdAt).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }) : ""}
                      </span>
                    </div>
                  </div>
                )
              })}
            </div>

            <footer className="p-6 bg-card/30 backdrop-blur-lg border-t border-border">
              <form onSubmit={handleSendMessage} className="max-w-4xl mx-auto flex gap-3 items-center">
                <div className="flex-1 relative group">
                  <Input value={inputValue} onChange={(e) => setInputValue(e.target.value)} placeholder="메시지를 입력하세요..." className="w-full h-14 bg-secondary/50 border-none rounded-2xl pl-6 pr-14 font-medium text-sm focus-visible:ring-2 focus-visible:ring-primary/20 transition-all placeholder:text-muted-foreground/40" />
                  <Button type="submit" size="icon" className="absolute right-2 top-2 h-10 w-10 rounded-xl bg-primary text-primary-foreground shadow-lg hover:scale-105 active:scale-95 transition-all" disabled={!inputValue.trim()}>
                    <Send className="h-4 w-4" />
                  </Button>
                </div>
              </form>
            </footer >
          </>
        ) : (
          <div className="flex-1 flex flex-col items-center justify-center p-12 text-center bg-background">
            <div className="w-24 h-24 rounded-[2.5rem] bg-secondary/30 flex items-center justify-center mb-8 border border-border shadow-inner">
              <MessageSquare className="h-10 w-10 text-muted-foreground/20" />
            </div>
            <h3 className="text-xl font-black tracking-tight">대화 상대를 선택하세요</h3>
            <p className="text-sm text-muted-foreground mt-3 max-w-[300px] leading-relaxed">사이드바에서 채널을 선택하여<br/>안전한 실시간 대화를 시작하세요.</p>
          </div>
        )}
      </main>

      <style jsx global>{`
        .custom-scrollbar::-webkit-scrollbar { width: 4px; }
        .custom-scrollbar::-webkit-scrollbar-track { background: transparent; }
        .custom-scrollbar::-webkit-scrollbar-thumb { background: hsl(var(--secondary)); border-radius: 10px; }
      `}</style>
    </div>
  )
}